.overall-report {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  box-sizing: border-box;
  &__date-picker {
    padding-top: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    & > input[type=date] {
      width: 26rem;
      height: 6.2rem;
      border: 0;
      padding: 0 2rem;
      font-size: 2rem;
      color: #000;
      margin: 0 2rem;
      border-radius: 8rem;
    }
  }
  &__total {
    text-align: center;
    font-size: 3rem;
    color: #000;
    position: relative;
    padding-bottom: 3rem;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__ranking {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 5rem;
    position: relative;
    margin-bottom: 5rem;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__group {
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      text-align: center;
      margin-bottom: 2rem;
    }
  }
  &__ranking-table {
    width: 30rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    td {
      line-height: 6rem;
      font-size: 2rem;
      color: #000;
      padding: 0 1rem;
      text-align: center;
    }
  }
  &__daily-work-table {
    width: 30rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    th,
    td {
      line-height: 6rem;
      font-size: 2rem;
      color: #000;
      padding: 0 1rem;
      text-align: center;
    }
  }
  &__daily-work {
    padding-bottom: 4rem;
  }
}