.service-special {
    &__add {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 4rem;
        .service-course-add__select-wrap {
            width: 27rem;
            height: 6rem;
            border-radius: 100px;
            margin: 0 2rem;
        }
        input {
            width: 27rem;
            height: 6rem;
            border-radius: 100px;
            margin: 0 2rem;
            background-color: #fff;
            border: 0;
            font-size: 2rem;
            padding: 0 4rem;
            color: #000;
            box-sizing: border-box;
            &::placeholder {
                color: #000;
            }
        }
        &__btn {
            background-color: #00679D;
            color: #fff;
            border-radius: 100px;
            width: 25rem;
            height: 5rem;
            display: block;
            margin: 0 auto 6.4rem;
        }
    }
    &__built {
        h3 {
            color: #1b97c8;
            font-size: 2.4rem;
            margin-bottom: 3rem;
        }
        &__group {

        }
    }
}

.service-special__add___btn {
    background-color: #00679D;
    color: #fff;
    border-radius: 100px;
    width: 25rem;
    height: 5rem;
    display: block;
    margin: 0 auto 6.4rem;
    border: 0;
    font-size: 2rem;
}

.service-special__built＿＿group {
    button {
        background-color: #00679D;
        color: #fff;
        border-radius: 100px;
        width: 9.2rem;
        height: 5rem;
        display: block;
        border: 0;
        margin: 0 auto;
    }
    td {
        padding: 0 !important;
    }
}