.vacation-control {
  position: relative;
  height: 7rem;
  background-color: #989696;
  display: flex;
  padding: 0 4rem;
  align-items: center;
  & > input {
    width: 22rem;
    height: 4.4rem;
    background-color: #fff;
    border-radius: 4rem;
    padding: 0 2rem;
    box-sizing: border-box;
    font-size: 2rem;
    border: 0;
  }
  &__days {
    display: flex;
    align-items: center;
    margin-right: 4rem;
  }
  &__btn-prev,
  &__btn-next {
    width: 3.6rem;
    height: 3.6rem;
    border: 0;
    background-color: #fff;
    border-radius: 0.6rem;
    position: relative;
    margin: 0 0.6rem;
    &::before {
      content: "";
      position: absolute;
      width: 1.3rem;
      height: 2.4rem;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%,-50%,0);
      background: transparent url("../images/icon-back.svg") no-repeat 0 0;
      background-size: 1.3rem auto;
    }
  }
  &__btn-next {
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(-180deg);
    }
  }
  &__current {
    font-size: 2.4rem;
    color: #000;
    padding: 0 2rem;
  }
}

.vacation-management {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  &__employee-wrap {
    padding: 5rem;
  }
  &__group {
    margin-bottom: 4rem;
    &:last-child {
      margin-bottom: 0;
    }
    h4 {
      font-size: 2.4rem;
      color: #1b97c8;
      margin-bottom: 2rem;
    }
  }
  &__wrap {
    padding-top: 0.7rem;
    display: flex;
    max-width: 120rem;
    flex-wrap: wrap;
  }
  &__employee-item {
    display: flex;
    margin-right: 2rem;
    margin-bottom: 2rem;
    & > input[type=checkbox] {
      width: 3rem;
      height: 3rem;
      border: solid 0.1rem #979797;
      background-color: #fff;
      padding: 0;
      margin-right: 0.8rem;
    }
    & > label {
      line-height: 3rem;
      height: 3rem;
      font-size: 2rem;
      color: #000;
    }
  }
  &__option {
    display: flex;
    gap: 4rem;
  }
  &__column {
    & > label {
      line-height: 3rem;
      height: 3rem;
      font-size: 2rem;
      color: #000;
      margin-right: 2rem;
    }
    & > input {
      width: 8rem;
      height: 4.4rem;
      background-color: #fff;
      border-radius: 0.8rem;
      padding: 0 2rem;
      box-sizing: border-box;
      font-size: 2rem;
      border: 0;
    }
  }
}
