:root {
  font-size: 62.5%;
  --color-admin-login-bg: #f8b62d;
  --color-admin-login-bg-d: #eba81e;
  --color-user-login-bg: #1b98c8;
  --color-user-login-bg-d: #1086b3;
  --color-title: #2b2b2b;
  --color-light-grey: #e2e2e2;
  --color-light-grey-l: #efefef;
  --color-light-grey-d: #cfcfcf;
  --color-grey: #6e6e6e;
  --color-grey-d: #525252;
  --color-wait: #fd9b9b;
  --color-wait-l: #edc3c3;
  --color-wait-bg: #4b4b4b;
  --color-timeout: #a7a7a7;
  --color-process: #a8dc73;
  --color-process-l: #b9d59d;
  --color-process-bg: #5b5b5b;
  --color-upcoming: #99bfd3;
  --color-upcoming-l: #bed6e3;
  --color-overdue: #897b67;
  --color-overdue-l: #b3947e;
  --color-overdue-bg: #363636;
  --color-reservation: #00679D;
  --color-consultant: #a9a9e6;
  --color-nurse: #fc926a;
  --color-beautician: #ff7c97;
  --color-control: #4ee2d7;
  --color-doctors: #7acef9;
  --color-nobody: #ff0000;
  --color-identity-text: #484848;
  --color-status-bg: #ececec;
  --color-card-footer: #d7d7d7;
  --color-card-btn-bg: #e9e9e9;
  --color-card-option: #979797;
  --color-card-process-now-bg: #ececec;
  --color-card-process-next-bg: #d7d7d7;
  --color-btn-search: #1b97c8;
  --color-reservation-search-hd: #989898;
  --color-reservation-search-content: #d8d8d8;
  --color-daily-report: #91b5b2;
  --color-daily-report-outpatient: #6b6b6b;
  --color-daily-report-outpatient-bg: #b9b9b9;
  --color-card-reservation: #cbcbcb;
  --color-reservation-client: #aeaeae;
  --color-reservation-client-btn: #c0c0c0;
  --color-week: #909090;
  --color-period-disable: #aaaaaa;
  --color-btn-cancel: #6a6a6a;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    height: 100%;
    @include susy-breakpoint($s-768, $c12) {
      overflow: hidden;
      width: 100%;
    }
}

body {
    height: 100%;
    overflow: hidden;
    &.scroll {
      overflow: auto;
    }
    @include susy-breakpoint($s-768, $c12) {
      width: 100%;
    }
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    border-radius: 8px;
  }
  
::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}

::-webkit-scrollbar-thumb {
  // background: #888;
  background: #c5c5c5;
  border: 0px none #ffffff;
  border-radius: 8px;
}

::-webkit-scrollbar-thumb:hover {
  background: #fff;
}

::-webkit-scrollbar-thumb:active {
  background: #ffffff;
}

::-webkit-scrollbar-track {
  // background: var(--color-reservation-search-content);
  border: 0px none #ffffff;
}

::-webkit-scrollbar-track:hover {
  // background: var(--color-reservation-search-content);
}

::-webkit-scrollbar-track:active {
  background: #111;
}

.site-search {
  width: 13rem;
  background-color: #fff;
  height: 3rem;
  border-radius: 3rem;
  display: flex;
  &--admin {
    position: fixed;
    right: 6rem;
    top: 11.6rem;
    width: 30rem;
    z-index: 40;
  }
  input {
    border: 0;
    line-height: 3rem;
    height: 3rem;
    background-color: transparent;
    color: #000;
    font-size: 1.4rem;
    width: calc(100% - 5rem);
    box-sizing: border-box;
    padding: 0 1.4rem;
  }
  button {
    text-align: center;
    display: inline-block;
    // width: 5.7rem;
    width: 5rem;
    height: 3rem;
    border-radius: 3rem;
    background-color: var(--color-btn-search);
    padding: 0;
    border: 0;
    color: #fff;
    font-size: 1.4rem;
  }
  @media screen and (max-width: 450px) {
    width: 11rem;
  }
}

.card-btn-menu {
  vertical-align: middle;
  width: 3.2rem;
  height: 3.2rem;
  // background: #fff url(../images/card-btn-menu.svg) no-repeat 0 0;
  background: #fff url(../images/more.svg) no-repeat center;
  background-size: 3.2rem;
  opacity: 1;
  border: 0;
  padding: 0;
  display: inline-block;
  border-radius: 50%;
  margin-left: auto;
  margin-right: 0;
  transition: all 300ms ease;
  &.is-active {
    opacity: 0.3;
  }
  @media screen and (max-width: 650px) {
    // margin-left: 1rem;
  }
}

.confirm-ui {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 30rem;
    height: 61rem;
    overflow: hidden;
    background-color: var(--color-user-login-bg);
    border-radius: 15rem;
    transform: translateY(-3rem);
  }
  &__icon {
    width: 15rem;
    height: 15rem;
    margin-top: 8rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__content {
    padding: 0 3rem;
    margin-bottom: 4rem;
    h2 {
      text-align: center;
      font-size: 2rem;
      color: #fff;
      font-weight: 500;
      margin-bottom: 1rem;
      line-height: 1.6;
    }
    p {
      font-size: 1.6rem;
      color: #fff;
      line-height: 1.6;
    }
  }
  &__btn-add,
  &__btn-add-other {
    background-color: #fff;
    width: 18rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: var(--color-grey-d);
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
    margin-bottom: 2rem;
  }
  &__btn-cancel,
  &__btn-finish {
    background-color: var(--color-btn-cancel);
    width: 13.6rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
}

.admin-confirm-ui {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 76rem;
    height: 30rem;
    overflow: hidden;
    background-color: var(--color-user-login-bg);
    border-radius: 15rem;
    display: flex;
    padding: 0 12rem;
    box-sizing: border-box;
    align-items: center;
  }
  &__icon {
    flex-basis: 15rem;
    margin-right: 11rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__right {
    flex-basis: 30rem;
    text-align: center;
  }
  &__content {
    padding: 0 3rem;
    margin-bottom: 3rem;
    h2 {
      text-align: center;
      font-size: 2rem;
      color: #fff;
      font-weight: 500;
      margin-bottom: 1rem;
      line-height: 1.6;
    }
    p {
      font-size: 1.6rem;
      color: #fff;
      line-height: 1.6;
      text-align: left;
    }
  }
  &__btn-delete {
    background-color: #fff;
    width: 11rem;
    display: inline-block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: var(--color-grey-d);
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
    margin-bottom: 2rem;
    margin: 0 1rem;
  }
  &__btn-cancel {
    background-color: var(--color-btn-cancel);
    width: 11rem;
    display: inline-block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
    margin: 0 1rem;
  }
}

.header-nav {
  background-color: #e0e0e0;
  position: absolute;
  top: 8rem;
  right: 0;
  padding: 3rem;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__btn {
    text-align: right;
    display: block;
    font-size: 3rem;
    color: #525252;
    margin-bottom: 2rem;
    transition: all 300ms ease;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      color: #00679d;
    }
    &.is-active {
      color: #00679d;
      pointer-events: none;
    }
    @include susy-breakpoint($s-768, $c12) {
      font-size: 2rem;
    }
  }
}
