.site-btn {
  height: 3.2rem;
  background-color: #fff;
  color: var(--color-grey-d);
  font-size: 1.4rem;
  line-height: 3.2rem;
  padding: 0 1.2rem;
  border-radius: 3.2rem;
  border: 0;
  transition: all 300ms ease;
  margin-right: 0.8rem;
  &:hover {
    background-color: var(--color-card-btn-bg);
  }
  &:active {
    background-color: var(--color-card-btn-bg);
  }
}

.admin-btn {
  box-sizing: border-box;
  text-align: center;
  width: 9.2rem;
  display: inline-block;
  height: 3.2rem;
  background-color: var(--color-user-login-bg);
  color: #fff;
  font-size: 1.4rem;
  line-height: 3.2rem;
  padding: 0 1.6rem;
  border-radius: 3.2rem;
  border: 0;
  transition: all 300ms ease;
  margin: 0 0.6rem
}

.admin-btn-add {
  box-sizing: border-box;
  text-align: center;
  display: inline-block;
  height: 4.5rem;
  background-color: var(--color-user-login-bg);
  color: #fff;
  font-size: 1.8rem;
  line-height: 4.5rem;
  padding: 0 3rem;
  border-radius: 3.2rem;
  border: 0;
  transition: all 300ms ease;
  margin: 0 1.4rem 0 0;
  vertical-align: middle;
}

.admin-btn-lg {
  box-sizing: border-box;
  text-align: center;
  width: 25rem;
  display: block;
  height: 5rem;
  background-color: #00679d;
  color: #fff;
  font-size: 1.8rem;
  line-height: 5rem;
  padding: 0 1.6rem;
  border-radius: 5rem;
  border: 0;
  transition: all 300ms ease;
  margin: 0 auto;
}

.admin-btn-return {
  width: 141px;
}

.site-btn-lg {
  height: 4rem;
  background-color: #fff;
  color: var(--color-grey-d);
  font-size: 1.8rem;
  line-height: 4rem;
  padding: 0 2.6rem;
  border-radius: 4rem;
  border: 0;
  transition: all 300ms ease;
  margin-right: 0.8rem;
  &:hover {
    background-color: var(--color-card-btn-bg);
  }
  &:active {
    background-color: var(--color-card-btn-bg);
  }
  @include susy-breakpoint($s-768, $c12) {
    padding-left: 1.2rem;
    padding-right: 1.2rem;
    font-size: 1.4rem;
  }
}

.admin-checkbox {
  width: 3rem;
  height: 3rem;
  display: block;
  position: relative;
  padding-left: 3rem;
  cursor: pointer;
  font-size: 1.4rem !important;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  line-height: 1.6 !important;
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 3rem;
    width: 3rem;
    background-color: #fff;
    border: solid 0.1rem #999;
    &::after {
      content: "";
      position: absolute;
      display: none;
    }
  }
  input:checked ~ .checkmark {
    background-color: #1b97c8;
    border-color: #1b97c8;
  }
  input:checked ~ .checkmark:after {
    display: block;
  }
  .checkmark {
    &::after {
      left: 1rem;
      top: 0.4rem;
      width: 0.8rem;
      height: 1.6rem;
      border: solid white;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }
}

.pop-ui-header {
  background-color: var(--color-reservation-search-hd);
  padding: 0 2rem;
  height: 4.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  h2 {
    font-size: 2rem;
    color: #fff;
    font-weight: 500;
  }
}

.pop-ui-btn-close {
  width: 1.8rem;
  background-color: transparent;
  height: 1.8rem;
  position: relative;
  border: 0;
  &::before,
  &::after {
    width: 2rem;
    height: 0.2rem;
    background-color: #fff;
    position: absolute;
    content: "";
    left: 50%;
    top: 50%;
  }
  &::before {
    transform: translate3d(-50%,-50%,0) rotate(45deg);
  }
  &::after {
    transform: translate3d(-50%,-50%,0) rotate(-45deg);
  }
}

.d-block { display: block !important; }

@media screen and (max-width: 991px) {
  .d-lg-none {
    display: none;
  }
}

@media screen and (max-width: 575px) {
  .d-sm-none {
    display: none;
  }
}