.employee-report {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  &__basic-info {
    padding-bottom: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__basic-info-inner {
    display: flex;
  }
  &__col {
    flex-basis: 100%;
    padding: 5rem;
    position: relative;
    h3 {
      color: #1b97c8;
      font-size: 3rem;
      line-height: 1.6;
    }
    h4 {
      color: #1b97c8;
      font-size: 2.4rem;
    }
  }
  &__date-picker {
    position: absolute;
    left: 30rem;
    top: 4rem;
    & > input[type=date] {
      width: 26rem;
      height: 6.2rem;
      border: 0;
      padding: 0 2rem;
      font-size: 2rem;
      color: #000;
      margin: 0 2rem;
    }
  }
  &__report-info {
    display: flex;
    padding-top: 4rem;
    align-items: center;
  }
  &__chart {
    width: 34rem;
    height: 34rem;
    margin-right: 6rem;
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 9.6rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
    }
  }
  &__work-list {
    margin-right: 6rem;
    p {
      line-height: 4.5rem;
      font-size: 2rem;
      margin-bottom: 1.6rem;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__result-list {
    margin-bottom: auto;
    padding-top: 5.6rem;
    p {
      color: #1b97c8;
      line-height: 4.5rem;
      font-size: 2rem;
      margin-bottom: 1.6rem;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__history {
    padding: 5rem 0;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
    &:last-child {
      &::after {
        display: none;
      }
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
      padding-left: 5rem;
    }
  }
  &__history-list {
    margin-bottom: 4rem;
  }
  &__history-item {
    padding: 4rem 6.4rem;
    &:nth-child(odd) {
      background-color: #bbbbbb;
    }
    &:nth-child(even) {
      background-color: #d8d8d8;
    }
  }
  &__date {
    span {
      font-size: 2rem;
      color: #000;
      line-height: 1.6;
      &:not(:last-child) {
        margin-right: 2rem;
      }
    }
  }
  &__place,
  &__task {
    font-size: 2rem;
    color: #000;
    line-height: 1.6;
  }
}