.login {
  background: var(--color-user-login-bg);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  &--admin {
    background: var(--color-admin-login-bg);
  }
}

.login-panel {
  width: 94.4rem;
  height: 37.8rem;
  border-radius: 37.8rem;
  background-color: #fff;
  padding: 0 16rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (max-width: 1280px) {
    width: 72rem;
    padding: 0 10rem;
  }
  @media screen and (max-width: 768px) {
    height: auto;
    width: 32rem;
    flex-wrap: wrap;
    padding: 10rem 5rem 3rem;
    &__form {
      width: 100%;
    }
  }
  &__left {
    padding-top: 0.6rem;
    @media screen and (max-width: 768px) {
      margin: 0 auto;
    }
  }
  &__logo {
    width: 20rem;
    margin-bottom: 3rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
    @media screen and (max-width: 768px) {
      margin: 0 auto 15rem;
    }
  }
  &__row {
    margin-bottom: 2rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__input {
    font-size: 1.8rem;
    color: var(--color-title);
    background: var(--color-light-grey);
    padding: 0 2.2rem;
    line-height: 4.8rem;
    height: 4.8rem;
    border-radius: 4.8rem;
    width: 26rem;
    border: 0;
    font-weight: 500;
    box-sizing: border-box;
    transition: all 300ms ease-in-out;
    &::placeholder {
      color: var(--color-grey);
    }
    &:focus {
      background: var(--color-light-grey-l);
    }
    @media screen and (max-width: 768px) {
      width: 100%;
    }
  }
  &__btn-login {
    border: 0;
    box-sizing: border-box;
    font-size: 1.8rem;
    color: #fff;
    background: var(--color-user-login-bg);
    padding: 0 2.2rem;
    line-height: 4.8rem;
    height: 4.8rem;
    border-radius: 4.8rem;
    width: 26rem;
    transition: all 300ms ease-in-out;
    margin-bottom: 1.6rem;
    &:hover {
      background: var(--color-user-login-bg-d);
    }
    &--admin {
      background: var(--color-admin-login-bg);
      &:hover {
        background: var(--color-admin-login-bg-d);
      }
    }
    @media screen and (max-width: 768px) {
      width: 100%;
    }
  }
  &__error-msg {
    font-size: 1.4rem;
    line-height: 1.6;
    color: var(--color-title);
    text-align: center;
    margin-bottom: 3rem;
    &:empty {
      margin-bottom: 0;
    }
  }
  &__title {
    text-align: center;
    font-size: 2.6rem;
    font-weight: 500;
    color: var(--color-title);
    line-height: 1.6;
    span {
      padding: 0.6rem;
    }
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
  &__checkbox {
    display: block;
    height: 3rem;
    text-align: center;
    input[type=checkbox] {
      position: absolute;
      left: -9999px;
      box-sizing: border-box;
      padding: 0;
    }
    input[type="checkbox"]:not(:checked)+label,
    input[type="checkbox"]:checked+label {
      height: 2.8rem;
      line-height: 2.8rem;
      display: inline-block;
      position: relative;
      padding-left: 3.6rem;
      cursor: pointer;
      font-size: 1.4rem;
      color: var(--color-title);
    }
    input[type="checkbox"]:not(:checked)+label:before,
    input[type="checkbox"]:checked+label:before {
      content: '';
      position: absolute;
      left: 0;
      width: 2.8rem;
      height: 2.8rem;
      border: solid 0.1rem var(--color-grey);
      background-color: transparent;
      border-radius: 50%;
      top: 0;
    }
    input[type="checkbox"]+label:after {
      background-color: var(--color-grey);
      border-radius: 50%;
      width: 1.8rem;
      height: 1.8rem;
      content: '';
      position: absolute;
      top: 0.6rem;
      left: 0.6rem;
      line-height: 0.8;
      transition: all .3s cubic-bezier(0.81, 0.01, 0.28, 1);
      opacity: 0;
    }
    input[type="checkbox"]:checked+label:after {
      opacity: 1;
    }
  }
}