.work-detail {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  box-sizing: border-box;
  &__col3 {
    width: 25%;
    font-size: 2rem;
    line-height: 1.8;
  }
  &__col6 {
    width: 50%;
    font-size: 2rem;
    line-height: 1.8;
  }
  &__col1_5 {
    width: 20%;
    font-size: 2rem;
    line-height: 1.8;
  }
  &__col2_5 {
    width: 40%;
    font-size: 2rem;
    line-height: 1.8;
  }
  &__col3_5 {
    width: 60%;
    font-size: 2rem;
    line-height: 1.8;
  }
  &__date-picker {
    padding-top: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    & > input[type=date] {
      width: 26rem;
      height: 6.2rem;
      border: 0;
      padding: 0 2rem;
      font-size: 2rem;
      color: #000;
      margin: 0 2rem;
      border-radius: 8rem;
    }
  }
  &__total {
    text-align: center;
    font-size: 3rem;
    color: #000;
    position: relative;
    padding-bottom: 3rem;
  }
  &__ranking {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 5rem;
    position: relative;
    margin-bottom: 5rem;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__group {
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      text-align: center;
      margin-bottom: 2rem;
    }
  }
  &__ranking-table {
    width: 30rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    td {
      line-height: 6rem;
      font-size: 2rem;
      color: #000;
      padding: 0 1rem;
      text-align: center;
    }
  }
  &__daily-work-table {
    width: 30rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    th,
    td {
      line-height: 6rem;
      font-size: 2rem;
      color: #000;
      padding: 0 1rem;
      text-align: center;
    }
  }
  &__daily-work {
    padding-bottom: 4rem;
  }
  &__wrap {
    padding: 5rem 0;
    position: relative;
    &::before {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      top: 0;
      content: "";
    }
    h3 {
      font-size: 2.4rem;
      margin-bottom: 2rem;
      color: #1b97c8;
      padding-left: 5rem;
    }
  }
  &__list {
    padding-bottom: 4rem;
    position: relative;
    &--empty {
      min-height: 40rem;
    }
  }
  &__work-item {
    padding: 4rem 6.4rem;
    &:nth-child(odd) {
      background-color: #bbbbbb;
    }
    &:nth-child(even) {
      background-color: #d8d8d8;
    }
    .color {
      &-blue {
        color: #002381;
      }
      &-red {
        color: #FF0000;
      }
      &-green {
        color: #016760;
      }
      &-brown {
        color: #970009;
      }
    }
  }
  &__report {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2.5rem;
    .work-detail__total {
      padding: 0;
    }
  }
  &__export {
    background-color: #00679D;
    padding: 0 3rem;
    margin-left: 3rem;
    line-height: 5rem;
    height: 5rem;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    &.deal-content {
      display: flex;
      font-size: 2rem;
      line-height: 1.8;
      .deal-content {
        &__title {
          width: 10rem;
        }
        &__content {
          width: calc(100% - 10rem);
          display: flex;
          flex-wrap: wrap;
          li {
            margin-right: 5rem;
          }
        }
      }
    }
  }
  &__date,
  &__status {
    font-size: 2rem;
    color: #000;
    line-height: 1.6;
    margin-right: 2rem;
  }
  &__status {
    &.is-complete {
      color: #417505;
    }
    &.is-timeout {
      color: #d0021b;
    }
    &.is-undone {
      color: #00679d;
    }
  }
  &__reservation {
    font-size: 2rem;
    line-height: 1.6;
    color: #808080;
  }
  &__client-name,
  &__doctor-name,
  &__start,
  &__end,
  &__time-consuming {
    font-size: 2rem;
    line-height: 1.6;
    color: #000;
    margin-right: 2rem;
  }
  &__empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    img {
      width: 20rem;
      display: block;
      margin: 0 auto 2rem
    }
    p {
      font-size:  1.8rem;
      color: #888888;
      text-align: center;
    }
  }
  &__filter {
    display: flex;
    margin-bottom: 4.8rem;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    input {
      width: 26rem;
      height: 6.2rem;
      border: 0;
      padding: 0 2rem;
      font-size: 2rem;
      color: #000;
      margin: 0 2rem;
      border-radius: 8rem;
    }
    button {
      background-color: #00679D;
      font-size: 1.8rem;
      width: 11.4rem;
      height: 5rem;
      border-radius: 100px;
      color: #fff;
      border: 0;
    }
  }
  &__select-wrap {
    width: 20rem;
    height: 6.2rem;
    line-height: 6.2rem;
    padding-right: 0;
    border-radius: 4rem;
    overflow: hidden;
    position: relative;
    &.result {
      width: 32rem;
    }
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__input {
    box-sizing: border-box;
    width: 32rem;
    height: 6.2rem;
    line-height: 6.2rem;
    font-size: 2rem;
    color: #000;
    border-radius: 4rem;
    border: 0;
    padding: 0 2rem;
  }
  &__btn-search,
  &__btn-clear {
    background-color: #00679d;
    border-radius: 4rem;
    color: #fff;
    border: 0;
    height: 4.8rem;
    padding: 0 3.2rem;
    font-size: 1.8rem;
    &.disable {
      background-color: #b6b6b6;
    }
  }
  &__deal {
    justify-content: center;
    margin-bottom: 10rem;
  }
}