.account-detail {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  &__basic-info {
    padding-bottom: 5rem;
    position: relative;
  }
  &__basic-info-inner {
    display: flex;
  }
  &__col {
    padding: 5rem;
    position: relative;
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 9.6rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
      &:disabled {
        background-color: transparent;
      }
    }
  }
  &__select-wrap {
    width: 29rem;
    height: 4.5rem;
    line-height: 4.5rem;
    padding-right: 0;
    border-radius: 0.5rem;
    overflow: hidden;
    position: relative;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 1rem;
      box-sizing: border-box;
    }
  }
}