.office-detail {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  &__basic-info {
    padding-bottom: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
      display: none;
    }
  }
  &__basic-info-inner {
    display: flex;
  }
  &__col {
    flex-basis: 50%;
    padding: 5rem;
    position: relative;
    &:first-child {
      &::after {
        width: 0.1rem;
        height: calc(100% - 10rem);
        background-color: #888888;
        position: absolute;
        right: 0;
        top: 5rem;
        content: "";
      }
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 9.6rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    .unit {
      width: 9.6rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
      padding-left: 1.4rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
      &.small {
        width: 93px;
      }
    }
    & > textarea {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      height: 9.6rem;
      font-size: 2rem;
      line-height: 1.6;
      padding: 1rem;
    }
  }
  &__select-wrap {
    width: 29rem;
    height: 4.5rem;
    line-height: 4.5rem;
    padding-right: 0;
    border-radius: 0.5rem;
    overflow: hidden;
    position: relative;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 1rem;
      box-sizing: border-box;
    }
  }
  &__avatar {
    display: flex;
    input[type=file] {
      color: transparent;
      &::-webkit-file-upload-button {
        visibility: hidden;
      }
      &::before {
        width: 13rem;
        height: 4.3rem;
        background-color: #1b97c8;
        border-radius: 1.6rem;
        content: '上傳照片';
        display: inline-block;
        text-align: center;
        line-height: 4.3rem;
        outline: none;
        white-space: nowrap;
        -webkit-user-select: none;
        cursor: pointer;
        font-size: 1.4rem;
        color: #fff;
      }
    }
  }
  &__picture-wrap {
    width: 15rem;
    height: 15rem;
    background-color: #fff;
    border-radius: 0.5rem;
    overflow: hidden;
    margin-right: 1rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
}