.service-type-add {
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  &__line {
    width: 100%;
    height: 2px;
    background-color: #AAAAAA;
  }
  &__add {
    padding: 5rem 0;
    position: relative;
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
      padding: 0 5rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    padding: 0 5rem;
    & > label {
      width: 9.6rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
      margin-right: 7rem;
    }
  }
  &__btn-save {
    border: 0;
    width: 13rem;
    height: 4.3rem;
    background-color: #1b97c8;
    border-radius: 1.6rem;
    display: inline-block;
    text-align: center;
    line-height: 4.3rem;
    font-size: 1.4rem;
    color: #fff;
    &.border-round {
      border-radius: 100px;
      background-color: #00679D;
      width: 250px;
    }
  }
}