.hint {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem;
    background-color: #1B97C8;
    p {
        margin: 0 3rem 0 0;
        color: #fff;
        font-size: 2rem;
    }
    button {
        height: 5rem;
        width: 25rem;
        border: 0;
        border-radius: 100px;
        background-color: #fff;
        color: #525252;
        font-size: 1.8rem;
    }
}


.pl-5 {
    padding-left: 5rem;
}

.service-flow {
    &__item {
        h3 {
            color: #1b97c8;
            font-size: 2.4rem;
            margin-bottom: 3rem;
        }
        h4 {
            font-size: 2rem;
            margin-bottom: 1rem;
            font-weight: normal;
        }
    }
    &__type {
        display: flex;
        align-items: center;
        margin-bottom: 5.2rem;
        padding: 0 5rem; 
        &__item {
            display: flex;
            align-items: center;
            label {
                font-size: 2rem;
                margin-left: 1.4rem;
                margin-right: 3rem;
            }
            &:first-child {
                margin-right: 16.6rem;
            }
        }
        .service-course-add__select-wrap {
            width: 25rem;
            height: 6rem;
            border-radius: 100px;
        }
    }
    &__flow {
        td {
            width: 16.5rem;
            text-align: center !important;
            padding: 0 !important;
            button {
                border: 0;
                border-radius: 100px;
                width: 9rem;
                height: 3.2rem;
                background-color: #1B97C8;
                color: #fff;
                font-size: 1.4rem;
            }
        }
        th {
            position: static !important;
        }
    }
    &__new {
        padding: 0 5rem; 
        background-color: #989696;
        &__item {
            padding: 1.5rem 5rem; 
        }
        &__title {
            padding: 2rem 0;
            color: #fff;
        }
        &__title, &__item {
            display: flex;
            align-items: center;
            font-size: 2rem;
            text-align: center;
            * {
                box-sizing: border-box;
            }
            >div {
                padding: 0 1rem;
            }
            input {
                background-color: #fff;
                border-radius: 0.5rem;
                border: 0;
                box-sizing: border-box;
                color: #000;
                font-size: 2rem;
                line-height: 4.5rem;
                height: 4.5rem;
                padding: 0 2rem;
                width: 100%;
            }
            .service-course-add__select-wrap {
                width: 100% !important;
            }
            button {
                border-radius: 100px;
                background-color: #1B97C8;
                color: #fff;
                height: 4.5rem;
                width: 100%;
                border: 0;
            }
        }
        &__icon {
            width: 7rem;
            img {
                width: 3rem;
                display: block;
                margin: 0 auto; 
            }
        }
        &__add {
            display: block;
            width: 19rem;
            height: 4.5rem;
            border-radius: 100px;
            background-color: #1B97C8;
            color: #fff;
            margin: 3.5rem auto 8.4rem;
            font-size: 1.8rem;
            border: 0;
        }
        &__save {
            display: block;
            width: 25rem;
            height: 5rem;
            border-radius: 100px;
            background-color: #00679D;
            color: #fff;
            margin: 0 auto 5rem;
            font-size: 1.8rem;
            border: 0;
        }
        &__num {
            width: 7rem;
        }
        &__char {
            width: 20rem;
        }
        &__equi {
            width: 16rem;
        }
        &__btn {
            width: 10rem;
        }
        &__step {
            width: 25rem;
            box-sizing: border-box;
        }
        &__time, &__score {
            width: calc((100% - 7rem - 7rem - 20rem - 16rem - 10rem - 25rem) / 2);
            box-sizing: border-box;
        }
    }
    &__setting {
        padding: 0 5rem 6rem;
        
        &__inner {
            display: flex;
            align-items: center;
            button {
                background-color: #1B97C8;
                border-radius: 100px;
                color: #fff;
                width: 9.2rem;
                height: 3.2rem;
                border: 0;
            }
        }
        &__item {
            display: flex;
            align-items: center;
            label {
                font-size: 2rem;
                margin-right: 2rem;
            }
            .service-course-add__select-wrap {
                width: 32rem;
            }
            &:first-child {
                margin-right: 9rem;
            }
            &:nth-child(2) {
                margin-right: 5.5rem;
            }
        }
    }
    &__save {
        .service-flow__new__save {
            margin-top: 5rem;
        }
    }
}