.daily-report {
  $self: &;
  overflow: hidden;
  display: grid;
  grid-template-rows: 8rem 1fr;
  height: 100vh;
  &__header {
    background-color: var(--color-daily-report);
    box-sizing: border-box;
    height: 8rem;
    padding: 0 3rem;
    position: relative;
    left: 0;
    top: 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    @include susy-breakpoint($s-768, $c12) {
      padding-left: 1.6rem;
      padding-right: 1.6rem;
      gap: 0.8rem;
    }
    &.beauty {
      background-color: #FFA5B8;
    }
    &__right {
      display: flex;
      align-items: center;
    }
  }
  &__btn-medical,
  &__btn-beauty {
    background-color: #fff;
    padding: 1.1rem 2rem;
    border-radius: 4rem;
    color: #525252;
    font-size: 1.8rem;
    box-sizing: border-box;
    @include susy-breakpoint($s-768, $c12) {
      font-size: 1.4rem;
      padding: 1.2rem;
    }
  }
  &__select-wrap {
    width: 15rem;
    height: 4rem;
    line-height: 4rem;
    border-radius: 0.2rem;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 3rem;
    @include susy-breakpoint($s-768, $c12) {
      width: 9rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      width: 13rem;
    }
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.6rem solid transparent;
      border-right: 0.6rem solid transparent;
      border-top: 1.2rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
      @include susy-breakpoint($s-768, $c12) {
        right: 0.6rem;
      }
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      border: 0;
      font-size: 1.8rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
      background-color: transparent;
      @include susy-breakpoint($s-768, $c12) {
        font-size: 1.4rem;
        padding-left: 0.8rem;
        padding-right: 0.8rem;
      }
    }
  }
  &__main {
    overflow-x: auto;
    overflow-y: auto;
    background-color: #d9d9d9;
    -webkit-overflow-scrolling: touch;
    height: 100%;
  }
  &__info {
    display: flex;
    align-items: center;
    gap: 1.2rem;
    @include susy-breakpoint($s-768, $c12) {
      gap: 0.8rem;
    }
    h2 {
      font-size: 3rem;
      color: #fff;
      font-weight: 500;
      @include susy-breakpoint($s-768, $c12) {
        font-size: 1.6rem;
      }
      @include susy-breakpoint($s-960, $c12) {
        font-size: 2rem;
      }
    }
  }
  &__doctor-name {
    font-size: 3rem;
    color: #fff;
    margin-left: 1rem;
    padding-left: 1rem;
    border-left: solid 0.2rem #fff;
    margin-right: 1rem;
  }
  &__btn-select-doctor {
    @extend .site-btn-lg;
  }
  &__date {
    display: flex;
    align-items: center;
  }
  &__btn-prev,
  &__btn-next {
    width: 3.6rem;
    height: 3.6rem;
    border: 0;
    background-color: #fff;
    border-radius: 0.6rem;
    position: relative;
    margin: 0 0.6rem;
    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-top: solid 1rem transparent;
      border-right: solid 1.6rem #000;
      border-bottom: solid 1rem transparent;;
      border-left: solid 1rem transparent;;
      left: 50%;
      top: 50%;
      transform: translate3d(-75%,-50%,0);
    }
    @include susy-breakpoint($s-768, $c12) {
      width: 3rem;
      height: 3rem;
      &::before {
        border-top: solid 0.8rem transparent;
        border-right: solid 1.2rem #000;
        border-bottom: solid 0.8rem transparent;;
        border-left: solid 0.8rem transparent;;
      }
    }
  }
  &__btn-next {
    &::before {
      transform: translate3d(-25%,-50%,0) rotate(180deg);
    }
  }
  &__current-date {
    font-size: 3rem;
    color: #fff;
    padding: 0 1.6rem;
    @include susy-breakpoint($s-768, $c12) {
      font-size: 1.4rem;
      padding-left: 0.6rem;
      padding-right: 0.6rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      font-size: 2rem;
    }
  }
  &__btn-select-date {
    @extend .site-btn-lg;
  }
  &__outpatient-wrap {
    height: 7.5rem;
    display: flex;
    align-items: center;
    z-index: 300;
    width: auto;
    position: sticky;
    top: 0;
    z-index: 0;
    span {
      display: block;
      background-color: var(--color-daily-report-outpatient);
      height: 7.5rem;
      font-size: 2.4rem;
      color: #fff;
      flex: 0 0 26rem;
      text-align: center;
      line-height: 7.5rem;
      &:nth-child(1) {
        flex: 0 0 13rem;
      }
    }
  }
  &__calendar {
    position: relative;
    padding-left: 13rem;
    padding-top: 12rem;
    background-color: var(--color-daily-report-outpatient-bg);
    display: flex;
  }
  &__time {
    position: absolute;
    left: 0;
    top: 12rem;
    background-color: #E5E5F7;
    background-image: linear-gradient(0deg, #d9d9d9 50%, #fff 50%);
    background-size: 13rem 60rem;
    div {
      box-sizing: border-box;
      width: 13rem;
      height: 10rem;
      position: relative;
      strong {
        font-weight: 500;
        position: absolute;
        font-size: 2.4rem;
        color: #000;
        left: 50%;
        top: 0;
        transform: translate3d(-50%,-50%,0);
      }
    }
  }
  &__outpatient {
    flex: 0 0 24rem;
    min-width: 0;
  }
  &__period {
    box-shadow: inset 0 0 0.1rem 0.05rem #fff;
    height: 10rem;
    &:last-child {
      opacity: 0.5;
    }
  }
  &__table {
    position: relative;
    tr {
      &:nth-child(6n+4),
      &:nth-child(6n+5),
      &:nth-child(6n+6) {
        td {
          &:first-child {
            background-color: #d9d9d9;
          }
        }
      }
    }
    th {
      font-size: 2.4rem;
      background-color: #6b6b6b;
      color: #fff;
      position: sticky;
      padding: 3rem 0;
      top: 0;
      z-index: 10;
    }
    td {
      min-width: 20rem;
      background-color: #c7c7c7;
      border: solid 0.1rem #fff;
      &:first-child {
        font-size: 2.4rem;
        color: #000;
        position: relative;
        background-color: #fff;
        span {
          position: absolute;
          top: 0;
          width: 100%;
          transform: translate3d(0,-50%,0);
          text-align: center;
          z-index: 0;
        }
      }
    }
  }
  &__new-period-wrap {
    box-sizing: border-box;
    padding-top: 2.4rem;
    background-color: #d9d9d9;
    // height: calc(100vh - 15.5rem);
    padding-right: 0;
    background-color: #e5e5f7;
    opacity: 0.8;
    background-image:  linear-gradient(#898989 0.1rem, transparent 0.1rem), linear-gradient(to right, #898989 0.1rem, #bfbfbf 0.1rem);
    background-size: 260px 130px;
    background-position: 129.5px 24px;
    #{$self}__new-period {
      &:nth-child(1) {
        flex: 0 0 13rem;
        outline: 0.05rem #767676 transparent;
        background-color: transparent;
        position: relative;
        span {
          position: absolute;
          text-align: center;
          font-size: 2.4rem;
          width: 100%;
          top: 0;
          left: 0;
          color: #000;
          transform: translate3d(0,-50%,0);
        }
      }
    }
    &.medical {
      #{$self}__new-period {
        padding: 0.8rem;
        position: relative;
      }
      #{$self}__new-period-row {
        min-height: 11rem;
      }
    }
  }
  &__new-period-row {
    position: relative;
    display: flex;
    min-height: 13rem;
    &:nth-child(odd) {
      #{$self}__new-period {
        &:nth-child(1) {
          background-color: #fff;
        }
      }
    }
    &:nth-child(even) {
      #{$self}__new-period {
        &:nth-child(1) {
          background-color: #d9d9d9;
        }
      }
    }
  }
  &__new-period {
    box-sizing: border-box;
    flex: 0 0 26rem;
    background-color: #b9b9b9;
    outline: 0.05rem #767676 solid;
    outline-offset: -0.05rem;
    &.disable {
      background-color: #979797;
      .new-period, .new-period__ps {
        background-color: #979797;
      }
    }
    &.medical {
      padding: 0.8rem;
    }
  }
}

.period {
  display: flex;
  background-color: #eaeaea;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 2rem;
  &:not(:last-child) {
    border-bottom: solid 0.3rem #ccc;
  }
  &__img {
    width: 3.6rem;
    margin-right: 1.6rem;
    padding-top: 0.6rem;
    img {
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
    }
  }
  &__row {
    margin-bottom: 0.6rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__name,
  &__date,
  &__mobile,
  &__course {
    font-size: 1.3rem;
    color: #000;
  }
  &__name {
    margin-right: 1rem;
  }
}

.new-period {
  background-color: #eaeaea;
  // display: grid;
  // grid-template-columns: 5rem 1fr;
  // grid-template-rows: 1fr 2.3rem;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  outline: 0.05rem #767676 solid;
  outline-offset: -0.05rem;
  // height: 13rem;
  &.done {
    opacity: 0.8;
  }
  &.medical {
    position: absolute;
    width: 25rem;
    // height: 13.4rem;
    height: auto;
    left: 0;
    bottom: 0;
    pointer-events: none;
    opacity: 0;
    transform: translate3d(0,106%,0);
    z-index: 100;
    outline: 0.05rem #767676 transparent;
    outline-offset: 0;
    // border: solid 0.1rem #767676;
    box-shadow: 0 0 3rem rgba(0,0,0,0.3);
    &.is-active {
      pointer-events: auto;
      opacity: 1;
    }
  }
  &__how {
    padding-left: 1rem;
    line-height: 2rem;
    font-weight: 500;
    &.color {
      &-purple {
        color: #953AE5;
      }
      &-orange {
        color: #FF6424;
      }
      &-green {
        color: #279D15;
      }
      &-deepgreen {
        color: #016760;
      }
    }
  }
  &__do {
    padding-left: 1rem;
    line-height: 2rem;
    font-weight: 500;
    &.color {
      &-blue {
        color: #002381;
      }
      &-brown {
        color: #A8715C;
      }
    }
  }
  &__btn-close {
    background-color: transparent;
    border: 0;
    width: 1.6rem;
    height: 1.6rem;
    top: 0.8rem;
    right: 0.8rem;
    position: absolute;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 1.6rem;
      height: 0.1rem;
      background-color: #525252;
      left: 50%;
      top: 50%;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
  }
  &__avatar-wrap,
  &__detail {
    padding: 0.5rem;
  }
  &__detail {
    padding-left: 0;
    width: calc(100% - 6rem);
  }
  &__avatar-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    box-sizing: border-box;
  }
  &__avatar {
    width: 4rem;
    height: 4rem;
    overflow: hidden;
    border-radius: 50%;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__btn-card-menu {
    width: 2.4rem;
    height: 2.4rem;
    background: #b6b6b6 url("../images/card-btn-menu.svg") no-repeat 0 0;
    background-size: 2.4rem;
    opacity: 1;
    border: 0;
    padding: 0;
    border-radius: 50%;
    transition: all 300ms ease;
    &.is-active {
      opacity: 0.3;
    }
  }
  &__card-option {
    position: absolute;
    left: -0.8rem;
    bottom: 0;
    width: 6.6rem;
    z-index: 50;
    opacity: 0;
    pointer-events: none;
    transition: all 300ms ease;
    transform: translate3d(0,100%,0);
    &.is-active {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__btn {
    width: 6.6rem;
    border: solid 0.1rem var(--color-card-option);
    border-bottom: 0;
    background-color: #fff;
    padding: 0;
    line-height: 3rem;
    height: 3.2rem;
    box-sizing: border-box;
    font-size: 1.4rem;
    color: var(--color-grey-d);
    position: relative;
    &:last-child {
      border-bottom: solid 0.1rem var(--color-card-option);
    }
    &:hover {
      background-color: #d7d7d7;
    }
    &:active {
      background-color: #d7d7d7 ;
    }
  }
  &__row {
    display: flex;
  }
  &__course {
    margin-right: 0.5rem;
    &::after {
      content: "/";
      color: #525252;
      font-size: 1.3rem;
      line-height: 1.6;
      padding-left: 0.4rem;
    }
  }
  &__name {
    max-width: calc(100% - 3rem - 6rem - 3rem);
  }
  &__name,
  &__number,
  &__vip,
  &__birthday,
  &__reserve,
  &__handle,
  &__course,
  &__status,
  &__occu,
  &__doctor {
    font-size: 1.3rem;
    color: #525252;
    line-height: 2rem;
  }
  &__birthday {
    position: relative;
    &::after {
      content: "/";
      font-size: 1.3rem;
      color: #000;
      padding-left: 0.2rem;
      padding-right: 0.2rem;
    }
  }
  &__name,
  &__number {
    color: #000;
  }
  &__vip,
  &__reserve {
    &::after {
      content: "/";
      font-size: 1.3rem;
      color: #000;
      padding-left: 0.2rem;
      padding-right: 0.2rem;
    }
  }
  &__status-wrap {
    display: grid;
    grid-template-columns: 6rem 1fr;
    align-items: center;
  }
  &__select-wrap {
    width: 100%;
    height: 2rem;
    line-height: 2rem;
    border-radius: 0.2rem;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    &.status-1 {
      background-color: #ffa9b3;
    }
    &.status-2 {
      background-color: #77e400;
    }
    &.status-3 {
      background-color: #77e400;
    }
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 0.8rem;
      width: 0; 
      height: 0; 
      border-left: 0.4rem solid transparent;
      border-right: 0.4rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      border: 0;
      font-size: 1.3rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 0.4rem;
      box-sizing: border-box;
      background-color: transparent;
    }
  }
  &__ps {
    grid-row-start: auto;
    grid-column-start: 1;
    grid-row-end: auto;
    grid-column-end: 3;
    background-color: #d9d9d9;
    font-size: 1.3rem;
    color: #525252;
    // display: flex;
    // align-items: center;
    padding-left: 1rem;
    // height: 2rem;
    width: 100%;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    ul {
      padding: 3px 0;
      li {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        line-height: 2.4rem;
      }
    }
  }
}

.period-label {
  position: relative;
  background-color: #fff;
  line-height: 2.4rem;
  display: flex;
  padding: 0 1.6rem;
  cursor: pointer;
  z-index: 0;
  &.done {
    opacity: 0.8;
  }
  &.is-active {
    z-index: 10;
    .new-period {
      pointer-events: auto;
      opacity: 1;
    }
  }
  &:not(:last-child) {
    margin-bottom: 0.8rem;
  }
  &::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 1rem;
    height: 100%;
  }
  &.status-1 {
    &::before {
      background-color: #ff8383;
    }
  }
  &.status-2 {
    &::before {
      background-color: #85ff00;
    }
  }
  &.status-3 {
    &::before {
      background-color: #85ff00;
    }
  }
  &.bg {
    &-dark {
      background-color: #939393;
    }
    &-blue {
      background-color: #A1D2FF;
    }
    &-purple {
      background-color: #D8B0FF;
    }
    &-red {
      background-color: #FFC5C8;
    }
  }
  &__name, &__host {
    max-width: 39px;
    // white-space: nowrap;
    // text-overflow: ellipsis;
    // overflow: hidden;
  }
  &__service {
    max-width: 84px;
    // white-space: nowrap;
    // text-overflow: ellipsis;
    // overflow: hidden;
  }
  &__name,
  &__number,
  &__host {
    font-size: 1.3rem;
    color: #000;
  }
  &__name, &__number {
    margin-right: 0.8rem;
  }
  &__host {
    margin-left: auto;
    margin-right: 0;
  }
}