.statistics-form {
    box-sizing: border-box;
    max-width: 48rem;
    margin: 0 auto;
    padding: 4.8rem 2.4rem;
    &__header {
        padding-bottom: 2rem;
    }
    &__title {
        font-size: 2.4rem;
        color: #000;
        text-align: center;
        line-height: 1.6;
    }
    &__date {
        text-align: center;
        font-size: 1.6rem;
    }
    &__list {
        display: grid;
        gap: 2.4rem;
        grid-template-columns: 1fr;
        padding: 2rem;
        border-top: solid 0.2rem #000;
        border-bottom: solid 0.2rem #000;
    }
    &__item {
        span {
            color: #000;
            font-size: 1.8rem;
            line-height: 1.6;
        }
    }
}