.other-announce {
    padding: 6.3rem 4.6rem;
    box-sizing: border-box;
    &:not(:first-child) {
        padding-top: 0;
    }
    input {
        border-radius: 0.5rem;
        background-color: #fff;
        padding: 4rem;
        font-size: 2rem;
        line-height: 1.5;
        width: 100%;
        height: 9rem;
        border: 0;
        border-radius: 5px;
        overflow: hidden;
        box-sizing: border-box;
    }
    &__content {
        border-radius: 0.5rem;
        background-color: #fff;
        padding: 4rem;
        font-size: 2rem;
        line-height: 1.5;
        width: 100%;
        border: 0;
        border-radius: 5px;
        overflow: hidden;
        box-sizing: border-box;
    }
    h3 {
        padding: 0;
    }
}

.other-message {
    &__table {
        margin-bottom: 3rem;
        th {
            top: 0 !important;
            &:nth-child(3) {
                text-align: left;
                padding-left: 5rem;
            }
            &:nth-child(4) {
                text-align: left;
            }
        }
        td {
            &:first-child {
                padding-left: 10rem;
            }
            &:nth-child(3) {
                text-align: left;
                padding-left: 5rem;
            }
            &:nth-child(4) {
                text-align: left;
            }
        }
    }
}

.backup-table {
    margin-bottom: 48px;
}