.wide-screen {
  display: grid;
  grid-template-columns: 110rem 234rem;
  height: 100%;
  overflow-y: hidden;
  overflow-x: auto;
}

.normal-screen {
  display: grid;
  grid-template-columns: 40rem 40rem 1fr;
  height: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  &.is-pad {
    grid-template-columns: 1fr;
  }
  @media screen and (max-width: 1024px) {
    grid-template-columns: 1fr;
  }
  @media screen and (max-width: 600px) {
    overflow-x: hidden;
  }
  &.full {
    grid-template-columns: 1fr;
  }
}

.panel {
  $self: &;
  position: relative;
  width: 100%;
  &--wait {
    background-color: var(--color-wait-bg);
    #{$self}__header {
      background-color: var(--color-wait);
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: minmax(min-content, max-content);
    }
  }
  &--process {
    background-color: var(--color-process-bg);
    #{$self}__header {
      background-color: var(--color-process);
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-auto-rows: minmax(min-content, max-content);
    }
  }
  &--upcoming {
    background-color: var(--color-wait-bg);
    padding-top: 2rem;
    #{$self}__header {
      background-color: var(--color-upcoming-l);
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: 1fr;
      grid-auto-rows: minmax(min-content, max-content);
    }
    @media screen and (max-width: 1024px) {
      display: none;
    }
  }
  &--overdue {
    background-color: var(--color-overdue-bg);
    padding-top: 2rem;
    #{$self}__header {
      background-color: var(--color-overdue);
      h2 {
        color: #fff;
      }
    }
    #{$self}__main {
      display: grid;
      grid-template-columns: 1fr;
      grid-auto-rows: minmax(min-content, max-content);
    }
    @media screen and (max-width: 1024px) {
      display: none;
    }
  }
  &--reservation {
    background-color: var(--color-wait-bg);
    #{$self}__header {
      background-color: var(--color-reservation);
      h2 {
        color: #fff;
      }
    }
    #{$self}__main {
      display: flex;
      justify-content: center;
      align-items: flex-start;
    }
  }
  &__header {
    box-sizing: border-box;
    height: 8rem;
    padding: 0 3rem;
    position: absolute;
    // position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    h2 {
      font-weight: 500;
      color: var(--color-grey-d);
      line-height: 8rem;
      font-size: 3rem;
      display: flex;
      align-items: center;
    }
    @media screen and (max-width: 1024px) {
      position: fixed;
    }
  }
  &__process {
    display: grid;
    grid-template-columns: 1fr 30rem;
    grid-auto-rows: minmax(min-content, max-content);
  }
  &__message-wrap {
    box-sizing: border-box;
    background-color: #4b4b4b;
    padding-top: 10rem;
    box-sizing: border-box;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
    height: 100vh;
    overflow-y: auto;
  }
  &__message {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  &__btn-back {
    background: transparent url("../images/icon-back-panel.svg") no-repeat 0 0;
    background-size: 2.3rem 4.4rem;
    width: 2.3rem;
    height: 4.4rem;
    border: 0;
    font-size: 0;
    margin-left: 0;
    margin-right: 2rem;
  }
  &__control {
    display: flex;
    align-items: center;
    cursor: pointer;
    &__icon {
      width: 40px;
      margin-right: 1.4rem;
      svg {
        width: 100%;
        path {
          transition: 0.3s;
        }
      }
      &:hover, &.active {
        svg {
          path {
            fill: #4B4B4B;
          }
        }
      }
    }
  }
  &__account {
    margin-right: 2rem;
    width: 15.6rem;
    height: 4rem;
    border-radius: 4rem;
    background-color: var(--color-control);
    display: flex;
    box-sizing: border-box;
    padding: 0.5rem 0.6rem;
    &.doctor {
      background-color: var(--color-doctors);
    }
    @include susy-breakpoint($s-768, $c12) {
      width: 11rem;
      margin-right: 0.8rem;
    }
    @include susy-breakpoint($s-960, $c12) {
      width: 14rem;
    }
  }
  &__account-avatar {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    margin-right: 1.2rem;
    @include susy-breakpoint($s-768, $c12) {
      margin-right: 0.6rem;
    }
  }
  &__account-name {
    font-size: 2rem;
    color: var(--color-identity-text);
    line-height: 3rem;
    @include susy-breakpoint($s-768, $c12) {
      font-size: 1.4rem;
    }
  }
  &__btn-toggle {
    width: 3.6rem;
    height: 3.6rem;
    position: relative;
    background-color: transparent;
    border: 0;
    padding: 0;
    &::before,
    &::after {
      width: 3.6rem;
      height: 0.6rem;
      background-color: #fff;
      top: 50%;
      left: 50%;
      content: "";
      position: absolute;
    }
    &::before {
      transform: translate3d(-50%,-300%,0);
    }
    &::after {
      transform: translate3d(-50%,200%,0);
    }
    span {
      width: 3.6rem;
      height: 0.6rem;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%,-50%,0);
    }
  }
  &__main {
    box-sizing: border-box;
    overflow-y: auto;
    padding: 8rem 3rem 0 3rem;
    gap: 2rem;
    height: calc(100vh - 2rem);
    &--card {
      padding-top: 10rem;
    }
    &--empty {
      padding-top: 10rem;
      position: relative;
      &::after {
        content: "目前沒有任務";
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2.4rem;
        color: #fff;
      }
    }
  }
}

.panel-message-ui {
  border-radius: 1rem;
  background-color: #313131;
  padding: 1.6rem 3.6rem 1.6rem 1.6rem;
  position: relative;
  &__btn-close {
    background-color: transparent;
    border: 0;
    width: 1.6rem;
    height: 1.6rem;
    top: 1.6rem;
    right: 1.6rem;
    position: absolute;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 2rem;
      height: 0.1rem;
      background-color: #70cdff;
      left: 50%;
      top: 50%;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
  }
  &__title {
    font-size: 1.6rem;
    color: #fff;
    line-height: 1.6;
    margin-bottom: 0.6rem;
    font-weight: 300;
  }
  &__text {
    font-size: 1.6rem;
    color: #fff;
    line-height: 1.6;
    margin-bottom: 1.2rem;
    font-weight: 300;
  }
  &__row {
    margin-bottom: 0.4rem;
    span {
      &:last-child {
        &::after {
          display: none;
        }
      }
    }
  }
  &__name,
  &__location,
  &__course,
  &__step,
  &__client {
    font-size: 1.4rem;
    color: #bababa;
    line-height: 1.6;
    font-weight: 300;
    &::after {
      content: "/";
      font-size: 1.4rem;
      color: #bababa;
      padding-left: 0.4rem;
    }
  }
  &__date {
    font-size: 1.4rem;
    color: #bababa;
    line-height: 1.6;
  }
  &__list {
    list-style: none;
    margin-bottom: 1.2rem;
    li {
      padding-left: 1.4rem;
      position: relative;
      font-size: 1.6rem;
      color: #fff;
      line-height: 1.6;
      font-weight: 300;
      &::before {
        width: 1.4rem;
        position: absolute;
        left: 0;
        top: 0;
        content: "-";
        font-size: 1.6rem;
        color: #fff;
      }
    }
  }
}