.reservation-search {
  width: 100%;
  max-width: 50rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%,calc(-50% - 4rem),0);
  border-radius: 1rem;
  overflow: hidden;
  pointer-events: none;
  opacity: 0;
  transition: all 300ms ease;
  @media screen and (max-width: 1300px) {
    width: calc(100% - 4rem);
    top: 10rem;
    transform: translateX(-50%);
  }
  @media screen and (max-width: 600px) {
    // top: 3rem;
    // transform: translateX(-50%);
  }
  &.is-active {
    pointer-events: auto;
    opacity: 1;
  }
  &__header {
    @extend .pop-ui-header;
  }
  &__btn-close {
    @extend .pop-ui-btn-close;
  }
  &__content {
    background-color: var(--color-reservation-search-content);
    padding: 4.6rem;
    h3 {
      font-size: 2.4rem;
      color: var(--color-grey-d);
      margin-bottom: 3rem;
      font-weight: 500;
    }
    @media screen and (max-width: 1300px) {
      padding: 4rem 3rem 3.5rem;
      // max-height: calc(100vh - 15rem - 4.6rem - 5rem);
      // overflow-y: auto;
      box-sizing: border-box;
    }
  }
  &__row {
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:first-child {
      margin-bottom: 7.6rem;
    }
    &:last-child {
      margin-bottom: 0;
      margin-top: 4.2rem;
    }
    label {
      display: inline-block;
      width: 8rem;
      font-size: 2rem;
      color: var(--color-grey-d);
      &.search-birthday {
        width: 12rem;
      }
    }
    input {
      box-sizing: border-box;
      width: 30rem;
      height: 4.6rem;
      line-height: 4.6rem;
      font-size: 2rem;
      color: var(--color-grey-d);
      border-radius: 4.6rem;
      padding: 0 2rem;
      border: 0;
      width: calc(100% - 13rem);
      &#search-birthday {
        // width: calc(100% - 12rem);
      }
    }
    @media screen and (max-width: 1300px) {
      label {
        width: 10rem;
      }
      input {
        width: calc(100% - 10rem);
      }
    }
  }
  &__btn-search {
    // background-color: var(--color-reservation);
    background-color: var(--color-btn-search);
    width: 25rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
}

.btn-reservation,
.btn-new {
  z-index: 500;
  width: 9.6rem;
  height: 9.6rem;
  background-color: var(--color-reservation);
  border-radius: 50%;
  position: fixed;
  bottom: 4.8rem;
  right: 4.8rem;
  border: 0;
  transition: all 300ms ease;
  &::before,
  &::after {
    content: "";
    width: 4.8rem;
    height: 0.8rem;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%,0);
  }
  &::after {
    transform: translate3d(-50%,-50%,0) rotate(90deg);
  }
  &.is-hide {
    opacity: 0;
    pointer-events: none;
  }
}

.reservation-box {
  position: absolute;
  width: calc(100vw - 80rem);
  height: calc(100vh - 8rem);
  right: 0;
  bottom: 0;
  overflow-y: auto;
  // padding-bottom: 2rem;
  @media screen and (max-width: 1024px) {
    width: 100%;
  }
}

.btn-reservation {
  @media screen and (max-width: 600px) {
    width: 7rem;
    height: 7rem;
    right: 3rem;
  }
}

.reservation-result {
  width: calc(100% - 40px);
  max-width: 58rem;
  padding: 3rem 0;
  position: absolute;
  top: 8rem;
  pointer-events: none;
  opacity: 0;
  left: 50%;
  transform: translate3d(-50%,0,0);
  &.is-active {
    pointer-events: auto;
    opacity: 1;
  }
  &__search {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
  }
  &__content {
    // height: 68rem;
    height: calc(100vh - 8rem - 3rem - 3rem - 2rem - 5rem);
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    @media screen and (max-width: 450px) {
      height: calc(100vh - 8rem - 3rem - 3rem - 2rem - 5rem - 10rem);
    }
  }
  &__empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,calc(-50% - 5rem),0);
    img {
      width: 12rem;
      display: block;
      margin: 0 auto 2rem
    }
    p {
      font-size:  1.8rem;
      color: #fff;
      text-align: center;
    }
  }
}

.checkBox {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #979797;
  position: relative;
  cursor: pointer;
  z-index: 1;
  input {
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  &::before {
    position: absolute;
    content: "";
    width: 1.8rem;
    height: 1.8rem;
    border-radius: 50%;
    background-color: #000;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.3s;
    opacity: 0;
    z-index: -1;
  }
  &.active {
    &::before {
      opacity: 1;
    }
  }
}

.reservation-client {
  position: absolute;
  left: 50%;
  top: 15rem;
  // top: 50%;
  transform: translate(-50%, 0);
  width: calc(100% - 4rem);
  max-width: 58rem;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  border-radius: 1rem;
  background-color: var(--color-reservation-client);
  max-height: calc(100vh - 15rem - 5rem);
  padding: 4rem;
  box-sizing: border-box;
  @media screen and (max-width: 991px) {
    top: 50%;
    transform: translate(-50%, calc(-50% + 4rem));
  }
  @media screen and (max-width: 600px) {
    padding: 3rem 2rem 2.5rem;
  }
  @media screen and (max-width: 450px) {
    top: 10rem;
    transform: translate3d(-50%,0,0);
    max-height: calc(100vh - 15rem - 5rem - 5rem);
  }
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__combo {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
    flex-wrap: wrap;
    * {
      box-sizing: border-box;
    }
  }
  &__check {
    display: flex;
    align-items: center;
    width: 11.8rem;
    cursor: pointer;
    .checkBox {
     
      margin-right: 0.7rem;
      
    }
    label {
      font-size: 1.6rem;
      font-weight: 500;
    }
  }
  &__select {
    width: 17.4rem;
    margin-right: 1.5rem;
    position: relative;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: 100px;
    padding: 0 1.6rem;
    height: 45px;
    border: 0;
    font-size: 1.6rem;
    appearance: none;
    background-image: url(/images/icon-arrow.svg);
    background-size: 1.8rem;
    background-position: calc(100% - 1rem) center;
    background-repeat: no-repeat;
    padding-right: 40px;
    &-active {
      display: flex;
      align-items: center;
      background-color: #fff;
      border-radius: 100px;
      padding: 0 1.6rem;
      height: 45px;
      border: 0;
      width: 17.4rem;
      span {
        color: #4B4B4B;
        width: calc(100% - 1.8rem);
        font-size: 1.6rem;
      }
      img {
        width: 1.8rem;
      }
    }
    &-list {
      position: absolute;
      top: calc(100% + 1.2rem);
      left: 0;
      width: 17.5rem;
      display: none;
      li {
        background-color: #fff;
        border: 1px solid #979797;
        padding: 0.6rem 1.5rem;
        color: #525252;
        font-size: 1.4rem;
        cursor: pointer;
      }
    } 
    @media screen and (max-width: 650px) {
      width: calc(100% - 11.8rem);
      padding-right: 0;
      margin: 0;
    }
  }
  &__input {
    width: calc(100% - 11.8rem - 17.4rem - 1.5rem);
    height: 4.5rem;
    line-height: 1;
    border-radius: 100px;
    background-color: #fff;
    padding: 0 1.6rem 0.3rem;
    border: 0;
    font-size: 1.6rem;
    &::placeholder {
      color: #4B4B4B;
      font-size: 1.6rem;
    }
    @media screen and (max-width: 650px) {
      width: 100%;
      margin-top: 1.4rem;
    }
  }
  &__content {
    margin-bottom: 4rem;
    &:last-child {
      margin-bottom: 0;
    }
    h3 {
      color: #fff;
      font-size: 2.4rem;
      font-weight: 500;
      margin-bottom: 2rem;
    }
  }
  &__client-info {
    background-color: var(--color-status-bg);
    border-radius: 1rem;
    overflow: hidden;
  }
  &__client-selected {
    height: 6.4rem;
    width: 100%;
    max-width: 25rem;
    background-color: #fff;
    border-radius: 6.4rem;
    padding: 1rem;
    box-sizing: border-box;
    margin-left: 0.6rem;
    display: flex;
    align-items: center;
  }
  &__avatar {
    width: 4.8rem;
    overflow: hidden;
    margin-right: 1rem;
    img {
      border-radius: 50%;
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__name {
    font-size: 3rem;
    color: #000;
  }
  &__mobile {
    font-size: 1.6rem;
  }
  &__row {
    height: 11rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1.5rem;
    span {
      font-size: 2.2rem;
      color: #000;
    }
    @media screen and (max-width: 650px) {
      span {
        display: none;
      }
    }
  }
  &__btn-client {
    background-color: var(--color-reservation-client-btn);
    font-size: 2rem;
    height: 5.6rem;
    line-height: 5.6rem;
    text-align: center;
    width: 100%;
    border: 0;
    color: var(--color-grey-d);
    position: relative;
    &::after {
      width: 100%;
      height: 100%;
      background: var(--color-user-login-bg) url("../images/icon-check.svg") no-repeat center center;
      background-size: 4rem auto;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      content: "";
    }
    &.is-complete {
      &::after {
        opacity: 1;
      }
    }
  }
  &__wrap {
    // margin-bottom: 2rem;
    // max-height: calc(100vh - 15rem - 8rem - 21rem - 2.4rem - 2rem - 4rem - 5rem);
    overflow-y: auto;
    max-height: calc(100vh - 15rem - 5rem - 21rem - 4rem - 8rem - 2.4rem - 2rem);
    @media screen and (max-width: 600px) {
      max-height: calc(100vh - 15rem - 5rem - 21rem - 4rem - 5.5rem - 2.4rem - 2rem);
      // height: calc(100vh - 15rem - 8rem - 21rem - 2.4rem - 2rem - 4rem - 5rem);
    }
    @media screen and (max-width: 450px) {
      max-height: calc(100vh - 15rem - 5rem - 21rem - 4rem - 5.5rem - 2.4rem - 2rem - 5rem);
    }
  }
  &__item {
    background-color: var(--color-status-bg);
    border-radius: 1rem;
    overflow: hidden;
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    // align-items: center;
    height: 9rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__step {
    font-size: 8rem;
    color: #000;
    opacity: 0.18;
    padding: 0 2rem;
    line-height: 9rem;
    @media screen and (max-width: 650px) {
      font-size: 6rem;
      padding: 0 1.2rem;
    }
  }
  &__detail {
    margin-right: auto;
    padding-top: 1.6rem;
    @media screen and (max-width: 650px) {
      padding-top: 1.8rem;
    }
  }
  &__type {
    font-size: 2.2rem;
    color: #000;
    line-height: 1.6;
    @media screen and (max-width: 650px) {
      font-size: 1.8rem;
    }
  }
  &__selected {
    color: var(--color-user-login-bg);
    font-size: 1.8rem;
    @media screen and (max-width: 650px) {
      font-size: 1.4rem;
      margin: 0;
    }
  }
  &__btn-select {
    width: 12rem;
    background-color: var(--color-reservation-client-btn);
    font-size: 2rem;
    color: var(--color-grey-d);
    padding: 0;
    border: 0;
    height: 9rem;
    position: relative;
    &::after {
      width: 100%;
      height: 100%;
      background: var(--color-user-login-bg) url("../images/icon-check.svg") no-repeat center center;
      background-size: 4rem auto;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      content: "";
    }
    &.is-complete {
      &::after {
        opacity: 1;
      }
    }
    @media screen and (max-width: 650px) {
      width: 5.6rem;
    }
  }
  &__btn-submit {
    // background-color: var(--color-reservation);
    background-color: var(--color-btn-search);
    width: 25rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
}

.create-client-data {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    position: relative;
  }
  &__inner {
    width: 100%;
    // max-width: 50rem;
    max-width: 58rem;
    border-radius: 1rem;
    overflow: hidden;
    margin: 0 auto 0;
    position: absolute;
    max-height: calc(100vh - 15rem - 5rem);
    transform: translate3d(-50%, 0, 0);
    left: 50%;
    top: 15rem;
    @media screen and (max-width: 991px) {
      top: 50%;
      transform: translate(-50%, calc(-50% + 4rem));
    }
    @media screen and (max-width: 600px) {
      width: calc(100% - 4rem);
    }
    @media screen and (max-width: 450px) {
      top: 10rem;
      transform: translate3d(-50%,0,0);
      max-height: calc(100vh - 15rem - 5rem - 5rem);
    }
  }
  &__header {
    @extend .pop-ui-header;
  }
  &__btn-close {
    @extend .pop-ui-btn-close;
  }
  &__content {
    background-color: var(--color-reservation-search-content);
    padding: 3.2rem;
    position: relative;
    // max-height: calc(100vh - 15rem - 5rem - 4.6rem);
    // overflow-y: auto;
    box-sizing: border-box;
    h3 {
      font-size: 2.4rem;
      color: var(--color-grey-d);
      margin-bottom: 3rem;
      font-weight: 500;
    }
    @media screen and (max-width: 650px) {
      padding-left: 2rem;
      padding-right: 2rem;
      h3 {
        margin-bottom: 1.5rem;
      }
    }
  }
  &__search {
    margin-bottom: 4rem;
    @media screen and (max-width: 450px) {
      margin-bottom: 2rem;
    }
  }
  &__row {
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    label {
      display: inline-block;
      width: 13rem;
      font-size: 2rem;
      color: var(--color-grey-d);
    }
    input, select {
      box-sizing: border-box;
      width: 30rem;
      height: 4.6rem;
      // line-height: 4.6rem;
      line-height: 1; 
      font-size: 2rem;
      color: var(--color-grey-d);
      border-radius: 4.6rem;
      padding: 0 2rem 0.3rem;
      border: 0;
      width: calc(100% - 13rem);
    }
    .create-client-data__select-wrap {
      width: calc(100% - 13rem);
      &::after {
        // display: none;
      }
    }
    select {
      // background-image: url(/images/icon-arrow.svg);
      // background-size: 1.8rem;
      // background-position: calc(100% - 1rem) center;
      // background-repeat: no-repeat;
      // appearance: none;
      // padding-right: 40px;
      option {
        padding-right: 15px;
      }
    }
    @media screen and (max-width: 1300px) {
      label {
        width: 10rem;
      }
      input, select {
        width: calc(100% - 10rem);
      }
    }
  }
  &__select-wrap {
    width: 30rem;
    height: 4.6rem;
    line-height: 4.6rem;
    padding-right: 0;
    border-radius: 4.6rem;
    overflow: hidden;
    position: relative;
    &::after {
      width: 2.4rem;
      height: 4.6rem;
      top: 0;
      right: 2rem;
      background: transparent url("../images/icon-arrow.svg") no-repeat center center;
      background-size: 2.4rem auto;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: var(--color-grey-d);
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__btn-search,
  &__btn-save {
    // background-color: var(--color-reservation);
    background-color: var(--color-btn-search);
    width: 25rem;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    height: 5rem;
    border-radius: 5rem;
    border: 0;
  }
  &__create {
    padding-top: 0.5rem;
  }
}

.built-data {
  height: calc(100vh - 15rem - 4.6rem - 3.2rem - 23.6rem - 4rem - 0.5rem - 2.4rem - 3rem - 3.2rem - 5rem);
  overflow-y: auto;
  @media screen and (max-width: 991px) {
    height: calc(100vh - 15rem - 5rem - 4.6rem - 3.2rem - 22.1rem - 2rem - 0.5rem - 2.4rem - 1.5rem - 3.2rem - 5rem);
    // height: calc(100vh - 5rem - 4.6rem - 3.2rem - 23.2rem - 4rem - 2.4rem - 3rem - 3.2rem - 2rem);
  }
}

.reservation-time {
  position: absolute;
  left: 50%;
  top: 15rem;
  width: calc(100% - 4rem);
  max-width: 58rem;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  border-radius: 1rem;
  background-color: var(--color-reservation-client);
  max-height: calc(100vh - 15rem - 5rem);
  transform: translate3d(-50%,0,0);
  padding: 4rem;
  box-sizing: border-box;
  @media screen and (max-width: 991px) {
    top: 50%;
    transform: translate(-50%, calc(-50% + 4rem));
  }
  @media screen and (max-width: 600px) {
    padding: 3rem 2rem 2.5rem;
  }
  @media screen and (max-width: 450px) {
    top: 10rem;
    transform: translate3d(-50%,0,0);
    max-height: calc(100vh - 15rem - 5rem - 5rem);
  }
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    h3 {
      color: #fff;
      font-size: 2.4rem;
      font-weight: 500;
    }
  }
  &__inform {
    width: 30px;
  }
  &__wrap {
    background-color: var(--color-reservation-search-content);
    border-radius: 1rem;
    padding: 2rem 4rem;
    // height: 62rem;
    height: calc(100vh - 15rem - 5rem - 8rem - 5rem - 5rem - 3rem);
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    margin-bottom: 3rem;
    @media screen and (max-width: 1400px) {
      padding: 2rem 1.5rem;
    }
    @media screen and (max-width: 600px) {
      overflow-x: auto;
      height: calc(100vh - 15rem - 5rem - 5.5rem - 5rem - 5rem - 3rem);
    }
    @media screen and (max-width: 450px) {
      height: calc(100vh - 15rem - 5rem - 5.5rem - 5rem - 5rem - 3rem - 5rem);
    }
  }
  &__date {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__btn-prev,
  &__btn-next {
    width: 3.6rem;
    height: 3.6rem;
    border: 0;
    background-color: #fff;
    border-radius: 0.6rem;
    position: relative;
    margin: 0;
    padding: 0;
    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-top: solid 1rem transparent;
      border-right: solid 1.6rem #000;
      border-bottom: solid 1rem transparent;
      border-left: solid 1rem transparent;
      left: 50%;
      top: 50%;
      transform: translate3d(-75%,-50%,0);
    }
    @media screen and (max-width: 600px) {
      width: 2.5rem;
      height: 2.5rem;
      &::before {
        border-top: solid 0.7rem transparent;
        border-right: solid 1.2rem #000;
        border-bottom: solid 0.7rem transparent;
        border-left: solid 0.7rem transparent;
      }
    }
  }
  &__range {
    width: calc(100% - 7.2rem);
    padding: 0 2rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    @media screen and (max-width: 600px) {
      width: calc(100% - 5rem);
      padding: 0 1.5rem;
    }
  }
  &__btn-prev {
    // margin-right: 2rem;
  }
  &__btn-next {
    &::before {
      transform: translate3d(-25%,-50%,0) rotate(180deg);
    }
  }
  &__current-date {
