.admin-main {
  width: 100%;
  padding-left: 20rem;
  box-sizing: border-box;
  align-items: center;
  padding-top: 9.6rem;
}

.admin-header {
  top: 0;
  position: fixed;
  padding-left: 5rem;
  padding-right: 5rem;
  align-items: center;
  height: 9.6rem;
  background-color: #414141;
  display: flex;
  width: calc(100% - 20rem);
  left: 20rem;
  box-sizing: border-box;
  justify-content: space-between;
  z-index: 200;
  h2 {
    font-size: 3rem;
    color: #fff;
    margin-right: auto;
  }
  &__btn-back {
    background: transparent url("../images/icon-back.svg") no-repeat 0 0;
    background-size: 2.3rem 4.4rem;
    width: 2.3rem;
    height: 4.4rem;
    border: 0;
    font-size: 0;
    margin-left: 0;
    margin-right: 2rem;
  }
  &__control {
    display: flex;
    align-items: center;
    
  }
  &__account {
    margin-right: 1rem;
    width: 21rem;
    height: 4rem;
    border-radius: 4rem;
    background-color: var(--color-control);
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
  }
  &__account-avatar {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    margin-right: 1.2rem;
    margin-left: 0.6rem;
  }
  &__account-name {
    font-size: 2rem;
    color: var(--color-identity-text);
    line-height: 4rem;
    margin-right: 1rem;
  }
  &__btn-logout {
    width: 7rem;
    height: 4rem;
    line-height: 4rem;
    font-size: 2rem;
    color: #484848;
    border: 0;
    transition: all 300ms ease;
    position: relative;
    &:active {
      background-color: #b6b6b6;
    }
  }
}
