.service-course-add {
  $self: &;
  height: 100vh;
  background-color: #dadada;
  overflow-y: scroll;
  padding-bottom: 6rem;
  &__basic-info {
    padding-bottom: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
  }
  &__basic-info-inner {
    display: flex;
  }
  &__col {
    flex-basis: 100%;
    padding: 5rem;
    position: relative;
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 16rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 2rem;
    }
  }
  &__select-wrap {
    width: 29rem;
    height: 4.5rem;
    line-height: 4.5rem;
    padding-right: 0;
    border-radius: 0.5rem;
    overflow: hidden;
    position: relative;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__flow {
    padding: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
    &:last-child {
      &::after {
        display: none;
      }
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__flow-wrap {
    margin-bottom: 6rem;
  }
  &__flow-item {
    margin-bottom: 6rem;
    display: flex;
    &:last-child {
      margin-bottom: 0;
    }
    #{$self}__select-wrap {
      width: 22rem;
      margin-right: 3rem;
    }
    & > input {
      width: 14rem;
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 2rem;
      margin: 0 1rem;
    }
  }
  &__step {
    flex-basis: 12rem;
    font-size: 2rem;
    color: #000;
    line-height: 1.6;
    display: flex;
    align-items: center;
  }
  &__time-wrap {
    display: flex;
    width: 30rem;
    margin-right: 2rem;
    align-items: center;
    & > input {
      min-width: 4rem;
      flex: 1;
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 2rem;
      margin: 0 0.4rem;
    }
  }
  &__continuous {
    padding: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__setting,
  &__saved {
    margin-bottom: 5rem;
    h4 {
      font-size: 2rem;
      color: #000;
      margin-bottom: 1rem;
    }
  }
  &__setting-wrap {
    display: flex;
    justify-content: flex-start;
    gap: 10rem;
    align-items: center;
    .admin-btn {
      margin-left: auto;
      margin-right: 0;
    }
    #{$self}__select-wrap {
      width: auto;
    }
    #{$self}__block {
      flex-basis: 40%;
    }
  }
  &__block {
    width: 100%;
    display: grid;
    grid-template-columns: 8rem 1fr;
    align-items: center;
    gap: 2rem;
    label {
      font-size: 2rem;
      color: #000;
    }
  }
  &__saved-item {
    display: flex;
    padding: 2rem;
    align-items: center;
    gap: 3rem;
    &:nth-child(odd) {
      background-color: #ebebeb;
    }
    &:nth-child(even) {
      background-color: #fafafa;
    }
    div {
      font-size: 2rem;
      color: #000;
      &:nth-child(1) {
        flex-basis: 30%;
      }
      &:nth-child(2) {
        flex-basis: 30%;
      }
    }
    .admin-btn {
      margin-left: auto;
      margin-right: 0;
    }
  }
  &__stocking {
    padding: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__stocking-block {
    display: grid;
    grid-template-columns: 10rem 1fr;
    margin-bottom: 4rem;
    h4 {
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    .admin-checkbox {
      position: relative;
      transform: translate3d(0,0.6rem,0);
    }
    & > input[type=checkbox] {
      width: 3rem;
      height: 3rem;
      border: solid 0.1rem #979797;
      background-color: #fff;
      padding: 0;
      position: relative;
      transform: translate3d(0,0.8rem,0);
    }
  }
  &__stocking-item-wrap {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.6rem;
  }
  &__stocking-item {
    input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 2rem;
      &:nth-child(1) {
        width: 34rem;
        margin-right: 7rem;
      }
      &:nth-child(2) {
        width: 22rem;
      }
    }
  }
  &__medical {
    padding: 5rem;
    position: relative;
    &::after {
      width: calc(100% - 10rem);
      height: 0.1rem;
      background-color: #888888;
      position: absolute;
      left: 5rem;
      bottom: 0;
      content: "";
    }
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__medical-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8rem;
    &:not(:last-child) {
      margin-bottom: 1.6rem;
    }
    #{$self}__select-wrap {
      width: auto;
    }
  }
  &__input-date {
    width: 100%;
    background-color: #fff;
    border-radius: 0.5rem;
    border: 0;
    box-sizing: border-box;
    color: #000;
    font-size: 2rem;
    line-height: 4.5rem;
    height: 4.5rem;
    padding: 0 2rem;
  }
  &__medical-interval {
    input {
      width: 8rem;
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 2rem;
    }
    span {
      font-size: 2rem;
      color: #000;
      padding-left: 1rem;
    }
  }
  &__beauty {
    padding: 5rem;
    position: relative;
    margin-bottom: 4rem;
    h3 {
      color: #1b97c8;
      font-size: 2.4rem;
      margin-bottom: 3rem;
    }
  }
  &__beauty-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8rem;
    &:not(:last-child) {
      margin-bottom: 1.6rem;
    }
    #{$self}__select-wrap {
      width: auto;
    }
  }
  &__beauty-interval {
    input {
      width: 8rem;
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 2rem;
    }
    span {
      font-size: 2rem;
      color: #000;
      padding-left: 1rem;
    }
  }
}

.service-course-setting {
  z-index: 300;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 96rem;
    background-color: #d8d8d8;
    &.small {
      width: 50rem;
    }
  }
  &__header {
    background-color: #989898;
    height: 4.8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    h3 {
      font-size: 2rem;
      color: #fff;
    }
  }
  &__btn-close {
    width: 1.8rem;
    background-color: transparent;
    height: 1.8rem;
    position: relative;
    border: 0;
    &::before,
    &::after {
      width: 2rem;
      height: 0.2rem;
      background-color: #000;
      position: absolute;
      content: "";
      left: 50%;
      top: 50%;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
  }
  &__content {
    padding: 3.6rem;
    h4 {
      font-size: 2rem;
      color: #1b97c8;
      margin-bottom: 2rem;
    }
  }
  &__row {
    display: flex;
    margin-bottom: 1.6rem;
    & > label {
      width: 12rem;
      font-size: 2rem;
      color: #000;
      line-height: 4.5rem;
    }
    & > input {
      background-color: #fff;
      border-radius: 0.5rem;
      border: 0;
      width: 29rem;
      box-sizing: border-box;
      color: #000;
      font-size: 2rem;
      line-height: 4.5rem;
      height: 4.5rem;
      padding: 0 1rem;
    }
  }
  &__select-wrap {
    width: 29rem;
    height: 4.5rem;
    line-height: 4.5rem;
    padding-right: 0;
    border-radius: 0.5rem;
    overflow: hidden;
    position: relative;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      right: 2rem;
      width: 0; 
      height: 0; 
      border-left: 0.8rem solid transparent;
      border-right: 0.8rem solid transparent;
      border-top: 0.8rem solid #000;
      content: "";
      position: absolute;
      pointer-events: none;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      text-indent: 1px;
      text-overflow: '';
      border: 0;
      font-size: 2rem;
      color: #000;
      border: 0;
      width: 100%;
      height: 100%;
      padding: 0 2rem;
      box-sizing: border-box;
    }
  }
  &__skill-wrap {
    padding-top: 0.7rem;
    display: flex;
    max-width: 71rem;
    flex-wrap: wrap;
  }
  &__skill-item {
    display: flex;
    margin-right: 2rem;
    margin-bottom: 2rem;
    & > input[type=checkbox] {
      width: 3rem;
      height: 3rem;
      border: solid 0.1rem #979797;
      background-color: #fff;
      padding: 0;
      margin-right: 0.8rem;
    }
    & > label {
      line-height: 3rem;
      height: 3rem;
      font-size: 2rem;
      color: #000;
    }
  }
  .substitute-time {
    margin: 0 2rem 0 0 !important;
    &-item {
      margin-bottom: 1.6rem;
      .modal-ui__input-time {
        height: 2.5rem;
        width: 11rem;
        background-color: #fff;
        border-radius: 100px;
        border: 0;
        padding: 0 1rem;
      }
    }
  }
}

.service-course-list,
.service-type-list {
  position: relative;
}