.card {
  $self: &;
  position: relative;
  &--wait {
    #{$self}__header {
      background-color: var(--color-wait-l);
    }
  }
  &--timeout {
    #{$self}__header {
      background-color: var(--color-timeout);
    }
  }
  &--process {
    #{$self}__header {
      background-color: var(--color-process-l);
    }
  }
  &--upcoming {
    #{$self}__header {
      background-color: var(--color-upcoming-l);
    }
    // &:first-child {
    //   margin-top: 2rem;
    // }
  }
  &--overdue {
    #{$self}__header {
      background-color: var(--color-overdue-l);
    }
    // &:first-child {
    //   margin-top: 2rem;
    // }
  }
  &__header {
    border-radius: 1rem 1rem 0 0;
    padding: 2rem 1.2rem 1rem 1.6rem;
    display: flex;
    flex-wrap: wrap;
    &.change {
      border-radius: 1rem;
      background-color: #868686;
    }
    &__top {
      display: flex;
    }
    &__bottom {
      display: flex;
      margin-top: 8px;
      width: 100%;
      span {
        display: block;
        width: 50%;
        font-size: 1.6rem;
        &:first-child {
          padding-right: 5px;
        }
        &:nth-child(2) {
          padding-left: 5px;
        }
      }
    }
  }
  &__changeDate {
    color: #016760;
  }
  &__cancel {
    color: #970009;
  }
  &__changeDate, &__cancel {
    font-size: 1.6rem;
    margin-left: auto;
    padding-top: 0.8rem;
    // padding-right: 1rem;
  }
  &__info {
    font-size: 0;
    display: flex;
    // align-items: center;
    // width: calc((100% - 6rem) * 0.35);
    // padding-left: 1rem;
  }
  &__detail {
    width: calc((100% - 6rem) * 0.65);
    padding-left: 1rem;
  }
  &__avatar-wrap {
    flex-basis: 6rem;
  }
  &__avatar {
    width: 6rem;
    height: 6rem;
    overflow: hidden;
    border-radius: 50%;
    // margin-right: 1rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__vip {
    padding-top: 0.6rem;
    width: 6rem;
    box-sizing: border-box;
    p {
      color: #000;
      text-align: center;
      &:nth-child(1) {
        font-size: 1.6rem;
        font-weight: 700;
      }
      &:nth-child(2) {
        font-size: 1.8rem;
        font-weight: 300;
      }
    }
  }
  &__timer {
    font-size: 1.6rem;
    color: #000;
    padding: 0 1.2rem;
    line-height: 3.2rem;
  }
  &__detail {
    flex: 1;
  }
  &__name {
    font-size: 2rem;
    color: #000;
    display: inline-block;
    // line-height: 3.2rem;
    line-height: 1.3;
    padding-top: 0.4rem;
    vertical-align: middle;
    margin-right: 0.8rem;
    width: 8rem;
    // overflow: hidden;
    // white-space: nowrap;
    // text-overflow: ellipsis;
  }
  &__course {
    margin-bottom: 0.4rem;
    padding-top: 0.4rem;
    // display: -webkit-box;
    // -webkit-box-orient: vertical;
    // -webkit-line-clamp: 2;
    // overflow: hidden;
  }
  &__course-name,
  &__doctor-name,
  &__course-number,
  &__date {
    color: #000;
    font-size: 1.6rem;
    line-height: 1.4;
  }
  &__course-name {
    &::after {
      content: "/";
      color: #000;
      font-size: 1.8rem;
      line-height: 1.6;
      padding-left: 0.4rem;
    }
  }
  &__doctor-name {
    font-weight: 900;
  }
  &__location {
    display: flex;
    font-size: 1.4rem;
    color: #000;
    align-items: center;
    gap: 0.1rem;
    line-height: 1;
    justify-content: flex-start;
    span {
      border: solid 0.1rem transparent;
      padding: 0.4rem 0.2rem;
      box-sizing: border-box;
      &:nth-child(1),
      &:nth-child(3) {
        max-width: 8rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      &:nth-child(2) {
        position: relative;
        width: 1.6rem;
        height: 2.6rem;
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 50%;
          width: 0; 
          height: 0;
          transform: translate3d(0,-50%,0); 
          border-top: 0.6rem solid transparent;
          border-bottom: 0.6rem solid transparent;
          border-left: 1.2rem solid #000;
        }
      }
    }
    &.step1 {
      span {
        &:nth-child(1) {
          border-color: #ff0000;
        }
      }
    }
    &.step2 {
      span {
        &:nth-child(2) {
          &::after {
            border-left-color: #ff0000;
          }
        }
      }
    }
  }
  &__flow {
    display: flex;
    font-size: 1.4rem;
    color: #000;
    align-items: center;
    gap: 0.1rem;
    justify-content: flex-start;
    span {
      border: solid 0.1rem transparent;
      padding: 0.4rem 0.2rem;
      box-sizing: border-box;
      &:nth-child(1),
      &:nth-child(3) {
        max-width: 7.6rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      &:nth-child(2) {
        position: relative;
        width: 1.6rem;
        height: 2.6rem;
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 50%;
          width: 0; 
          height: 0;
          transform: translate3d(0,-50%,0); 
          border-top: 0.6rem solid transparent;
          border-bottom: 0.6rem solid transparent;
          border-left: 1.2rem solid #000;
        }
      }
    }
    &.step1 {
      span {
        &:nth-child(1) {
          border-color: #ff0000;
        }
      }
    }
    &.step2 {
      span {
        &:nth-child(2) {
          &::after {
            border-left-color: #ff0000;
          }
        }
      }
    }
  }
  &__switch {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
    margin-bottom: 0.4rem;
  }
  &__btn-prev,
  &__btn-next {
    width: 2.4rem;
    height: 2.4rem;
    background-color: #fff;
    border-radius: 2.4rem;
    position: relative;
    border: 0;
    &::before {
      position: absolute;
      content: "";
      width: 0.6rem;
      height: 0.6rem;
      border-top: solid 0.1rem #000;
      border-right: solid 0.1rem #000;
      left: 50%;
      top: 50%;
    }
  }
  &__btn-prev {
    &::before {
      transform: translate3d(-30%,-50%,0) rotate(-135deg);
    }
  }
  &__btn-next {
    &::before {
      transform: translate3d(-70%,-50%,0) rotate(45deg);
    }
  }
  &__status {
    background: var(--color-status-bg);
    padding: 1.4rem;
    display: grid;
    grid-template-columns: 6.2rem auto;
    gap: 1rem;
  }
  &__status-detail {
    font-size: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    span {
      display: flex;
      font-size: 1.4rem;
      color: #000;
      line-height: 1.6;
      strong {
        font-weight: normal;
      }
    }
  }
  &__footer {
    background-color: var(--color-card-footer);
    padding: 1.4rem 2rem;
    font-size: 0;
    border-radius: 0 0 1rem 1rem;
    display: flex;
    flex-wrap: wrap;
    gap: 1.2rem;
  }
  &__process-identity-wrap {
    display: flex;
    flex-wrap: wrap;
    gap: 1.2rem;
  }
  &__btn {
    @extend .site-btn;
    margin-right: 0;
    &:last-child {
      margin-right: 0;
    }
    &--assign {
      background: #eb2860;
      color: #fff;
      padding: 0 1.6rem 0 3rem;
      position: relative;
      font-size: 1.4rem;
      &::before {
        width: 1.6rem;
        height: 1.6rem;
        background: transparent url("../images/icon-drop.svg") no-repeat center center;
        background-size: 1.6rem;
        position: absolute;
        top: 50%;
        left: 0.6rem;
        transform: translate3d(0,-50%,0);
        content: "";
      }
      &:hover {
        background: #eb2860;
      }
    }
    &--people {
      background: #eb2860;
      color: #fff;
      padding: 0 1.6rem 0 3rem;
      position: relative;
      font-size: 1.4rem;
      &::before {
        width: 1.6rem;
        height: 1.6rem;
        background: transparent url("../images/icon-people.svg") no-repeat center center;
        background-size: 1.6rem;
        position: absolute;
        top: 50%;
        left: 0.6rem;
        transform: translate3d(0,-50%,0);
        content: "";
      }
      &:hover {
        background: #eb2860;
      }
    }
    &--add {
      background-color: #fff;
      color: #000;
      font-size: 1.4rem;
      width: 8.8rem;
      &:hover {
        background-color: #fff;
      }
    }
    &--consultant {
      background-color: var(--color-consultant);
    }
    &--nurse {
      background-color: var(--color-nurse);
    }
    &--beautician {
      background-color: var(--color-beautician);
    }
    &--control {
      background-color: var(--color-control);
    }
    &--doctors {
      background-color: var(--color-doctors);
    }
    &--nobody {
      color: #fff;
      background-color: var(--color-nobody);
    }
  }
  &__process {
    padding: 1.4rem 2rem;
    &--now {
      background-color: var(--color-card-process-now-bg);
    }
    &--next {
      background-color: var(--color-card-process-next-bg);
      border-radius: 0 0 1rem 1rem;
    }
  }
  &__process-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.4rem;
  }
  &__process-title,
  &__process-course,
  &__process-time {
    font-size: 1.6rem;
    line-height: 2.2rem;
    color: #000;
    font-weight: 500;
  }
  &__process-title {
    text-transform: uppercase;
    margin-right: 1rem;
    font-weight: bolder;
  }
  &__process-course {
    margin-right: auto;
  }
}

.status-square {
  flex-basis: 5rem;
  width: 5rem;
  height: 5rem;
  border-radius: 1.2rem;
  box-sizing: border-box;
  margin: 0 auto;
  &--a {
    background-color: #eb2860;
  }
  &--b {
    background-color: #ff7826;
  }
  &--c {
    background-color: #febc01;
  }
  &--d {
    background: transparent url("../images/status-d.jpg") no-repeat 0 0;
    background-size: 5rem;
    border: solid 0.1rem #000;
  }
}

.identity {
  text-align: center;
  box-sizing: border-box;
  // min-width: 8.8rem;
  vertical-align: middle;
  height: 3.2rem;
  display: inline-block;
  line-height: 3.4rem;
  border-radius: 3.2rem;
  padding: 0 0.8rem;
  color: var(--color-identity-text);
  font-size: 1.5rem;
  margin-right: 0.8rem;
  &--consultant {
    background-color: var(--color-consultant);
  }
  &--nurse {
    background-color: var(--color-nurse);
  }
  &--beautician {
    background-color: var(--color-beautician);
  }
  &--control {
    background-color: var(--color-control);
  }
  &--doctors {
    background-color: var(--color-doctors);
  }
  &--nobody {
    color: #fff;
    background-color: var(--color-nobody);
  }
}

.card-option {
  position: absolute;
  right: -1.6rem;
  top: 5.5rem;
  width: 9.2rem;
  z-index: 50;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__btn {
    width: 9.2rem;
    border: solid 0.1rem var(--color-card-option);
    border-bottom: 0;
    background-color: #fff;
    padding: 0;
    line-height: 3rem;
    height: 3.2rem;
    box-sizing: border-box;
    font-size: 1.4rem;
    color: var(--color-grey-d);
    position: relative;
    &:last-child {
      border-bottom: solid 0.1rem var(--color-card-option);
    }
    &:hover {
      background-color: #d7d7d7;
    }
    &:active {
      background-color: #d7d7d7 ;
    }
  }
}

.card-reservation {
  border-radius: 1rem;
  background-color: var(--color-card-reservation);
  margin-bottom: 2rem;
  padding: 1rem 2rem;
  display: flex;
  // align-items: center;
  position: relative;
  @media screen and (max-width: 650px) {
    flex-wrap: wrap;
    padding: 1rem;
  }
  &__avatar {
    flex-basis: 6rem;
    height: 6rem;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 1rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
    @media screen and (max-width: 650px) {
      flex-basis: 7.4rem;
      height: 7.4rem;
      margin-right: 0;
    }
  }
  &__detail {
    flex: 1;
    @media screen and (max-width: 650px) {
      margin-top: 1.4rem;
    }
  }
  &__info {
    margin-right: 1rem;
    @media screen and (max-width: 650px) {
     width: calc(100% - 7.4rem);
     margin: 0;
     padding: 0 0 0 1rem;
     box-sizing: border-box;
    }
  }
  &__row {
    display: flex;
    // align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 650px) {
      justify-content: flex-start;
    }
  }
  &__name {
    font-size: 2.2rem;
    color: #000;
    display: inline-block;
    line-height: 3.2rem;
    vertical-align: middle;
    margin-right: 1rem;
    max-width: 110px;
    @media screen and (max-width: 600px) {
      max-width: calc(100% - 11rem);
    }
    // overflow: hidden;
    // white-space: nowrap;
    // text-overflow: ellipsis;
  }
  &__doctorName {
    font-size: 1.6rem;
  }
  &__course-name {
    color: #000;
    font-size: 1.8rem;
    line-height: 1.6;
    width: 16rem;
    @media screen and (max-width: 600px) {
      width: 100%;
    }
    // display: -webkit-box;
    // -webkit-box-orient: vertical;
    // -webkit-line-clamp: 1;
    // overflow: hidden;
  }
  &__course-number {
    color: #000;
    font-size: 2.2rem;
    line-height: 3.2rem;
  }
  &__date {
    margin-right: 0.9rem;
  }
  &__date,
  &__time {
    color: #000;
    font-size: 1.8rem;
    line-height: 3.2rem;
    @media screen and (max-width: 650px) {
      font-size: 1.6rem;
    }
  }
  &__time {
    margin-right: 3rem;
  }
  &__status {
    font-size: 16px;
    &.notarrive {
      color: #953AE5;
    }
    &.change {
      color: #016760;
    }
    &.cancel {
      color: #970009;
    }
    &.wait {
      color: #A8715C;
    }
    @media screen and (max-width: 650px) {
      position: absolute;
      top: 17px;
      right: 13px;
    }
  }
  &__tag {
    display: flex;
    @media screen and (max-width: 650px) {
        margin-left: auto;
    }
  }
}