.side-bar {
  $self: &;
  width: 20rem;
  background-color: #707070;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 5.2rem 0;
  box-sizing: border-box;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 400;
  &__logo {
    width: 12.4rem;
    height: 3.8rem;
    margin-bottom: 4rem;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
  &__nav {
    width: 100%;
    margin-bottom: auto;
    ul {
      li {
        position: relative;
        margin-bottom: 4rem;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &__btn {
    display: block;
    text-align: center;
    font-size: 2rem;
    display: block;
    color: #c3c3c3;
    transition: all 300ms ease;
    &:hover {
      color: #fff;
    }
    &.is-active {
      color: #fff;
    }
    // &.is-active~#{$self}__sub-nav {
    //   opacity: 1;
    //   pointer-events: auto;
    // }
  }
  &__sub-nav {
    position: absolute;
    width: 9.2rem;
    right: -4rem;
    top: -2.5rem;
    z-index: 100;
    opacity: 0;
    transition: all 300ms ease-in-out;
    pointer-events: none;
    &.is-active {
      opacity: 1;
      pointer-events: auto;
    }
    a {
      display: block;
      width: 9.2rem;
      height: 3.2rem;
      line-height: 3.2rem;
      border: solid 0.1rem #979797;
      text-align: center;
      font-size: 1.4rem;
      color: #525252;
      background-color: #fff;
      transition: all 300ms ease;
      &:last-child {
        border-top: 0;
      }
      &:hover {
        background-color: #d7d7d7;
      }
      &.is-active {
        background-color: #d7d7d7;
      }
    }
  }
  &__btn-logout {
    font-size: 2rem;
    display: block;
    margin-bottom: 0;
    color: #fff;
    transition: all 300ms ease;
    margin-right: auto;
    margin-left: 5.4rem;
    background-color: transparent;
    border: 0;
    &:hover {
      color: #fff;
    }
  }
}