.loader {
    width: 100%;
    position: fixed;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 500;
    opacity: 0;
    pointer-events: none;
    transition: all 300ms ease;
    &.is-active {
        pointer-events: auto;
        opacity: 1;
    }
    &__inner {
        width: 20rem;
        height: 20rem;
        background-color: rgba(0,0,0,0.5);
        border-radius: 1.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    svg path,
    svg rect {
        fill: #fff;
    }
}